  background-color: rgb(255, 255, 255);
}
.wp-block-button.background-dark:hover::after {
  color: rgb(23, 26, 34);
}
.wp-block-button.background-dark .wp-block-button__link:hover {
  color: rgb(23, 26, 34);
}
.wp-block-button::before {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  width: 4.6875rem;
  height: 100%;
  transform: scale(1);
  transition: 500ms ease;
  border-radius: 2rem;
}
.wp-block-button::after {
  content: "\e910";
  position: absolute;
  top: calc(50% + 0.5px);
  right: 1.5625rem;
  transform: translateY(-50%);
  transition: 500ms ease;
  color: rgb(255, 255, 255);
  font-family: "icomoon";
  font-size: 0.6875rem;
  line-height: 1;
}
.wp-block-button:hover {
  color: rgb(255, 255, 255);
}
.wp-block-button:hover::before {
  width: 100%;
  transform: scale(1.5);
  transition: 1s;
  border-radius: 0;
}
.wp-block-button__link {
  display: block;
  position: relative;
  padding: 0.75rem 3.125rem 0.75rem 2.5rem;
  background-color: transparent;
  color: rgb(255, 255, 255);
}
